<script setup lang="ts">
import { computed } from "vue";
import { useI18n } from "vue-i18n";
import { ACol, ASelect, ACheckbox } from "@actindo/core-ui";
import type {
  IDataTypeInstance,
  ICacheEntitiesAttributeInSetCache,
} from "@/services/api/DataHub/Attributes";
import type { ITenantAttributeLanguage } from "@/services/api/DataHub/Languages";
import type { ITenantAttributeScope } from "@/services/api/DataHub/Scopes";
import type { IDataTypesDateTimeValueConfiguration } from "./models";

defineProps<{
  dataTypeInstance: IDataTypeInstance<IDataTypesDateTimeValueConfiguration>;
  remoteScopes?: ITenantAttributeScope[];
  remoteLanguages?: ITenantAttributeLanguage[];
  remoteAttributes?: ICacheEntitiesAttributeInSetCache[];
  readonly?: boolean;
}>();

const configuration = defineModel<IDataTypesDateTimeValueConfiguration>();

const DATE_FORMATS = {
  none: "datahub-plugin.dateTime.dateFormatNone",
  short: "datahub-plugin.dateTime.dateFormatShort",
  medium: "datahub-plugin.dateTime.dateFormatMedium",
  long: "datahub-plugin.dateTime.dateFormatLong",
};

const TIME_FORMATS = {
  none: "datahub-plugin.dateTime.timeFormatNone",
  short: "datahub-plugin.dateTime.timeFormatShort",
  medium: "datahub-plugin.dateTime.timeFormatMedium",
};

const { t } = useI18n();

const formatToItem = (formats: Record<string, string>) =>
  Object.entries(formats).map(([value, title]) => ({ value, title: t(title) }));

const dateFormats = computed(() => formatToItem(DATE_FORMATS));
const timeFormats = computed(() => formatToItem(TIME_FORMATS));
</script>

<template>
  <ACol>
    <ASelect
      v-model="configuration.dateFormat"
      :items="dateFormats"
      :label="t('datahub-plugin.dateTime.dateFormat')"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ASelect
      v-model="configuration.timeFormat"
      :items="timeFormats"
      :label="t('datahub-plugin.dateTime.timeFormat')"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ACheckbox
      v-model="configuration.readOnly"
      :label="t('datahub-plugin.dateTime.readOnly')"
      :readonly="readonly"
    />
  </ACol>
</template>

<i18n>
{
  "en": {
    "datahub-plugin": {
      "dateTime": {
        "readOnly": "Schreibgeschützt",
        "dateFormat": "Date format",
        "timeFormat": "Time format",
        "dateFormatNone": "Do not show date",
        "dateFormatShort": "Short (e.g. 02/11/2019)",
        "dateFormatMedium": "Medium (e.g. February 11, 2019)",
        "dateFormatLong": "Long (e.g. Monday, February 11, 2019)",
        "timeFormatNone": "Do not show time",
        "timeFormatShort": "Short (e.g. 09:00 PM)",
        "timeFormatMedium": "Medium (e.g. 09:00:00 PM)"
      }
    }
  },
  "de": {
    "datahub-plugin": {
      "dateTime": {
        "readOnly": "Read only",
        "dateFormat": "Datumsformat",
        "timeFormat": "Zeitformat",
        "dateFormatNone": "Datum nicht anzeigen",
        "dateFormatShort": "Kurz (z.B. 02.11.2019)",
        "dateFormatMedium": "Mittel (z.B. 11. Februar 2019)",
        "dateFormatLong": "Lang (z.B. Montag, 11. Februar 2019)",
        "timeFormatNone": "Zeit nicht anzeigen",
        "timeFormatShort": "Kurz (z.B. 21:00 Uhr)",
        "timeFormatMedium": "Mittel (z.B. 21:00:00 Uhr)"
      }
    }
  }
}
</i18n>
